<template>
  <div>
    <a @click="showDataList()" type="primary">IP</a>

    <Drawer
      title="ip"
      width="512"
      :closable="false"
      v-model="showDataListState"
    >
      <Button @click="showAddFrom = true" type="info"></Button>
      <Divider />
      <Table :columns="columns" :data="listData">
        <template slot-scope="{ row, index }" slot="action">
          <a @click="deleteData(row.id, index)"></a>
        </template>
      </Table>
    </Drawer>

    <Drawer
      title=""
      :closable="false"
      @on-close="onCloseAddView"
      v-model="showAddFrom"
    >
      <Form
        :label-width="80"
        ref="addDataParam"
        :rules="validateRule"
        :model="addDataParam"
      >
        <FormItem label="ip" prop="ipAddress">
          <i-input
            placeholder=""
            v-model="addDataParam.ipAddress"
          ></i-input>
        </FormItem>
        <FormItem>
          <Button type="primary" @click="handleSubmit('addDataParam')"
            ></Button
          >
        </FormItem>
      </Form>
    </Drawer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      paramData: {
        data: { ipAddress: '' },
        page: {
          currentPage: 1,
          pageSize: 10
        },
        sort: {
          orderBy: 'id',
          sort: 'desc'
        }
      },
      addDataParam: {
        ipAddress: ''
      },
      validateRule: {
        ipAddress: [
          {
            required: true,
            pattern: /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/,
            message: 'ip',
            trigger: 'blur'
          }
        ]
      },
      showDataListState: false,
      showAddFrom: false,
      columns: [
        {
          title: 'ip',
          key: 'ipAddress'
        },
        { title: '', slot: 'action', width: 250, align: 'center' }
      ],
      listData: []
    }
  },
  mounted: function() {},
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.commitData()
        } else {
          this.$Message.error('!')
        }
      })
    },
    showDataList() {
      this.showDataListState = true
      if (this.listData.length == 0) {
        this.getDataList()
      }
    },
    getDataList() {
      this.utils.netUtil.post(
        this.API_PTAH.ipWhtieListFind,
        this.paramData,
        response => {
          this.listData = response.data.data
        }
      )
    },
    deleteData(id, index) {
      this.$Modal.confirm({
        title: '',
        content: '',
        onOk: () => {
          this.utils.netUtil.post(
            this.API_PTAH.ipWhtieListDelete,
            { id: id },
            () => {
              this.listData.splice(index, 1)
              this.$Message.success('!')
            }
          )
        }
      })
    },
    commitData() {
      this.utils.netUtil.post(
        this.API_PTAH.ipWhtieListAdd,
        this.addDataParam,
        () => {
          this.addDataParam.ipAddress = ''
          this.showAddFrom = false
          this.$Message.success('!')
          this.getDataList()
        }
      )
    },
    onCloseAddView() {
      this.addDataParam.ipAddress = ''
    }
  }
}
</script>

<style></style>
